import * as React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import GithubIcon from '@material-ui/icons/Github';
import Layout from 'gatsby-theme-aoi/src/layout';

function ThirdPage() {
  return (
    <Layout
      title="Without Drawer Example"
      maxWidth="md"
      disableDrawer
      fab={
        <Fab color="primary" href="https://github.com">
          <GithubIcon />
        </Fab>
      }
    >
      <Typography variant="h3" component="h2" gutterBottom>
        Without Drawer Example
      </Typography>
      <Typography paragraph>This is an example layout without drawer.</Typography>
      <Button component={GatsbyLink} to="/" color="secondary">
        Go to Index Page
      </Button>
      <Button component={GatsbyLink} to="/page-2/" color="primary">
        Go to Second Page
      </Button>
    </Layout>
  );
}

export default ThirdPage;
